/**
 * WordPress dependencies
 */
import { Icon } from '@wordpress/components';

export default function NextgenMenuIcon( props ) {
	const {
		icon,
		label,
		slug,
	} = props;

	return (
		<span
			className="nextgen-menu-icon"
			id={ `nextgen-menu-icon-${ slug }` }>
			<Icon className="nextgen-menu-icon__icon" icon={ icon } />
			<span className="nextgen-menu-icon__label">
				{ label }
			</span>
		</span>
	);
}
